.mypageImgCircle{
  margin: 50px auto 0;
  text-align: center;
}
.mypageTxt{
text-align: center;
}
.mypagName{
margin: 15px 0 ;
font-size: 22px;
}
.mypageLogOutBtn{
width: 80px;
height: 25px;
background-color: #DC3644;
color: #fff;
border: none;
border-radius: 20px;
margin-left: 5px;
}
.mypageUpdateBtn{
  width: 100px;
height: 25px;
border: none;
border-radius: 20px;
background-color: #ddd;
}
.mypageLinkBox{
  display: flex;
  justify-content: space-evenly;
  border-radius: 25px;
  align-items: center;
  margin: 30px 20px 0;
}
.mypageLink{
  display: flex;
  flex-direction: column;
  text-decoration: none;
  text-align: center;
  color: #000;
  border: 1px solid #ddd;
  border-radius: 10px;
  width: 105px;
  height: 100px;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
}
.mypageLink svg{
  margin: 0 auto;
  width: 24px;
  height: 24px;
}
.mypageLink span{
  font-size: 16px;
  margin-top: 5px;
}
.mypageInfoBox{
  margin: 20px 20px 0;
}
.mypageInfoBox ul{
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}
.mypageInfoBox li{
  flex: 1 1 40%;
  display: flex;
  padding: 15px 10px ;align-items: center;
}
.mypageInfoBox svg{
  width: 16px;
  height: 16px;
}
.mypageInfoBox p{
  margin: 0;
  margin-left: 10px;
}

.mypageCallBox{
  background-color: #ddd;
  border-radius: 10px;
  margin: 0 20px;
  padding: 15px 20px;
}
.mypageCallBox a{
  color: #000;
  font-weight: bold;
  font-size: 16px;
}
.mypageCallBox p{
  color: #2f2f2f;
  font-size: 14px;
  margin: 8px 0;
}


/*  결제 리스트 확인 페이지 */

.paymentlistBox{
  width: 390px;
  height: 844px;  
  overflow-y: scroll;
  position: relative;
}
.paymentlistBox::-webkit-scrollbar {
  width: 0px;
}
.paymentlistTop{
  position: fixed;
  text-align: center;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
  width: 390px;
  z-index: 333;
}
.paymentlistTop h2{
  margin: 15px 0;
  font-size: 20px;
}
.paymentlistTop button{
  position: absolute;
  top: 15px;
  left: 10px;
  background-color: transparent;
  border: none;
}
.paymentlistTop button svg{
  width: 24px;
  height: 24px;
}
.paymentlist{
  margin: 0 20px 0;
  margin-top: 15px;
}
.paymentlistItemBox{
  margin-top: 75px;
  margin-bottom: 100px;
}
.paymentlistItem{
  display: flex;
  position: relative;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 15px;
}
.paymentlistItem img{
  width: 80px;
  height: 110px;
}
.paymentlistTxt{
  margin-left: 10px;
  margin-top: 5px;
}
.paymentlistTxt p{
  margin: 0;
  width: 160px;
  font-size: 14px;
  margin-bottom: 5px;
}
.paymentlistDeleteBtn{
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background-color: transparent;
}
.paymentlistDeleteBtn svg{
  width: 22px;
  height: 22px;
}